
















export default{
	data(){
		return{
			upNewPhone:"",
			upPhonePwd:"",
			Signboard:'',
			Signguid:''
		}
	},
	onLoad:function () {
		var jsonList=uni.getStorageSync("jsonList");
		var data = JSON.parse(jsonList); //JSON字符串转对象
		this.Signboard = data.Ident_Signboard;
		this.Signguid = data.Ident_Signguid;
	},
	methods:{
		newPhone:function(evt){
			this.upNewPhone=evt.target.value;
		},
		phonePwd:function(evt){
			this.upPhonePwd=evt.target.value;
		},
		changePhone(evt){
			if(this.upNewPhone==""||this.upPhonePwd==""){
				uni.showToast({
					title:'请将信息填写完整',
					icon:"none"
				})
			}else if(!RegExp(/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9]|17[0|1|2|3|5|6|7|8|9])\d{8}$/).test(this.upNewPhone)){
				uni.showToast({
					title:'填写的电话格式不正确',
					icon:"none"
				})
			}else if(!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.upPhonePwd)){
				uni.showToast({
					title:"密码只能由字母数字组成,长度不能小于6位大于16位",
					icon: "none"
				})
			}else{
				uni.request({
					url:'http://192.168.199.194/mobile/Security/updatePhone',
					method:'GET',
					data:{
						phone:this.upNewPhone,
						pwd:this.upPhonePwd,
						Ident_Signboard: this.Signboard,
						Ident_Signguid: this.Signguid
					},
					success: (res) => {
						//判断返回的状态  0--成功,1--失败，2--密码错误
						if(res.data.status == 0){
							uni.showToast({
								title: res.data.message,
								icon:'none',
							})
						}else{
							uni.showToast({
								title: res.data.message,
								icon:'none',
							})
						}
					}
				})
			}
		}
	}
}
